import React, { Component } from 'react';

import Image from "./Image";
import fpu from "../images/fpu.png";

class Info extends Component {
    constructor(props) {
        super(props);
        this.handleLoad = this.handleLoad.bind(this);
    }

    componentDidMount() {
        window.addEventListener('load', this.handleLoad);
    }

    componentWillUnmount() {
        window.removeEventListener('load', this.handleLoad);
    }

    load = (backgroundColor, textColor) => {
        this.props.appChangeColor(backgroundColor, textColor);
    }

    handleLoad() {
        // set the default colouring on load
        this.load("red", "white")
    }

    render() {
        return (
            <div className="centered-column side-margins"  >
                <p>Atlas Kolapsu je súborom experimentov skúmajúcich problematiku environmentálnych data vizualizácií. Projekt skúma rozličené, častokrát protichodné, estetické formy reprezentácie realít, ktoré dané dáta prezentujú. Cieľom projektu je skúmanie nových spôsobov vizuálnej komunikácie tak komplexného problému, akým je klimatický kolaps. Veríme, v exaktnosť a silu vedy, no taktiež vychádzame z premisy, že na vedeckú prácu stojacu za samotnými datámi, by mali nadviazať umelci, dizajnéri, no taktiež environmentálny psychológovia či iné humanitné profesie a pracovať s nimi tak, aby boli čo najkomunikatívnejšie pre širokú verejnosť.</p>
                <p>Atlas Kolapsu je work in progress. Všetky obrazové materiály vám radi sprístupníme zadarmo, kontaktujte nás na info@atlaskolapsu.sk</p>
                <img src={fpu} alias="FPU logo" width="200px" height="auto" />
                <p id="fond-text">Projekt z verejných zdrojov podporil Fond na podporu umenia.<br></br>Fond na podporu umenia nezdpovedá za obsah stránky.</p>
            </div>
        );
    }
}

export default Info;